body::after {
    content: "";
    background-color: #0b1e46cc;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.8s ease 0s;
    pointer-events: none;
    z-index: 149;
}
.popup-show body::after {
    opacity: 1;
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 30px 10px;
    transition: visibility 0.8s ease 0s;
    visibility: hidden;
    pointer-events: none;
}
.popup_show {
    z-index: 150;
    visibility: visible;
    overflow: auto;
    pointer-events: auto;
}
.popup_show .popup__content {
    visibility: visible;
    opacity: 1;
}
.popup__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100%;
    flex: 1 1 auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.popup__content {
    position: relative;
    visibility: hidden;
    opacity: 0;
    transition:
        opacity 0.3s ease 0s,
        height 0.3s ease-in-out;
    background-color: #f6faff;
    padding: 40px;
    width: 100%;
    width: 454px;
    max-width: 454px;
    border-radius: 8px;
    .lock & {
        visibility: visible;
    }
    @media (max-width: $mobile) {
        padding: 30px 15px;
    }
}

.popup__close {
    position: absolute;
    right: -41px;
    top: 10px;
    display: block;
    background: transparent;
    svg {
        fill: #fff;
    }
    @media (max-width: $mobile) {
        right: 0;
        top: -28px;
    }
}

.popup {
    // .popup__body

    &__body {
    }

    // .popup__top

    &__top {
        margin-bottom: 2rem;
    }

    // .popup__top-title

    &__top-title {
        font-weight: 400;
        font-size: 1.125rem;
        line-height: 1.75rem;
        color: #222d41;
        margin: 0px 0px 10px 0px;
        @media (max-width: $mobileSmall) {
            font-size: 16px;
            margin: 0px 0px 5px 0px;
        }
    }

    // .popup__title

    &__title {
        font-size: 18px;
        margin-bottom: 1.5rem;
        @media (max-width: $mobile) {
            font-size: 16px;
            margin-bottom: 1rem;
        }
    }

    // .popup__price

    &__price {
        display: flex;
        margin-top: 4px;
        align-items: center;
        color: #222d41;
        font-weight: 400;
        font-size: 1.25rem;
        line-height: 1.5rem;
        span {
            font-family: "Roboto", sans-serif;
            margin: 0px 0px 0px 5px;
        }
    }

    // .popup__center

    &__center {
    }

    // .popup__inputs

    &__inputs {
        position: relative;
        background-color: #e4ebf3;
        border-radius: 0.75rem;
        padding: 1rem;
        display: flex;
        align-items: center;
        height: 56px;
        transition: color 0.3s ease 0s;
        border: 1px solid transparent;

        &._form-focus {
            box-shadow: rgba(46, 113, 252, 0.77) 0px 0px 0px 0.767286px inset;
            background-color: rgba(236, 241, 247, 0.23);
            label {
                color: #222d41;
                font-size: 0.8156rem;
                position: relative;
                transform: translateY(-0.1rem);
            }
        }
        @media (max-width: $mobile) {
            padding: 5px 10px;
        }
    }

    // .popup__input

    &__input {
        display: flex;
        flex-direction: column;
        width: 100%;
        overflow: hidden;
        label {
            line-height: 1.2;
            position: absolute;
            transition-property: all;
            transition-duration: 0.3s;
            transition-timing-function: ease-in-out;
            color: #222d4185;
        }
        &.input_expire {
            opacity: 0;
            transform: translate3d(100%, 0, 0);
            transition-property: transform;
            transition-duration: 150ms;
            transition-timing-function: ease-in-out;
            position: absolute;
            pointer-events: none;
            .active & {
                opacity: 1;
                pointer-events: auto;
                transform: translate3d(6.5625rem, 0, 0);
            }
        }
        &.input_cvv {
            opacity: 0;
            transform: translate3d(100%, 0, 0);
            transition-property: transform;
            transition-duration: 150ms;
            transition-timing-function: ease-in-out;
            position: absolute;
            pointer-events: none;
            .active & {
                opacity: 1;
                pointer-events: auto;
                margin-left: 7.1875rem;
                transform: translate3d(6.5625rem, 0, 0);
                @media (max-width: $mobileSmall) {
                    margin-left: 60px;
                }
            }
        }
        &.popup-card {
            input {
                transition-property: transform;
                transition-duration: 150ms;
                transition-timing-function: ease-in-out;
                transform: translate3d(0, 0, 0);
                width: 100%;
                .active & {
                    opacity: 0;
                    pointer-events: none;
                    width: 180px;
                    transform: translate3d(-50%, 0, 0);
                }
                &::placeholder {
                    opacity: 0;
                    color: #222d4185;
                }
                &._form-focus {
                    &::placeholder {
                        opacity: 1;
                    }
                }
            }
        }
        input {
            background: transparent;
            position: relative;
            z-index: 5;
            font-size: 16px;
            &::placeholder {
                color: #222d4185;
            }
        }
        .t-value {
            position: absolute;
            top: 1.7rem;
            width: 100px;
            @media (max-width: $mobile) {
                top: 1.9rem;
                font-size: 12px;
            }
        }
    }

    // .popup__icons

    &__icons {
        position: absolute;
        right: 0.75rem;
        top: 50%;
        transform: translate(0, -50%);
        display: flex;
        align-items: center;
        z-index: 10;
        img {
            height: 1.5rem;
            margin-right: 0.25rem;
            @media (max-width: $mobileSmall) {
                margin: 0px 0px 0px 0px;
                height: 1rem;
            }
        }
        @media (max-width: $mobileSmall) {
            right: 5px;
        }
        .payment-mastercard {
            display: none;
        }
        .payment-visa {
            display: none;
        }
        .payment-maestro {
            display: none;
        }
        .payment-close {
            display: none;
            width: 24px;
            height: 24px;
            cursor: pointer;
            opacity: 0.56;
            justify-content: center;
            align-items: center;
            svg {
                fill: rgba(34, 45, 65, 0.596);
            }
            @media (max-width: $mobileSmall) {
                width: 20px;
                height: 20px;
            }
        }
    }

    // .popup__button

    &__button {
        margin-top: 1.5rem;
        margin-bottom: 1rem;
        font-weight: 400;
        width: 100%;
        letter-spacing: 0.2px;
        pointer-events: auto;
        opacity: 1;
        border-radius: 0.5rem;
        background-color: #2e71fc;
        color: #ffffff;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 56px;
        padding: 10px;

        @media (max-width: $mobile) {
            height: 50px;
            margin: 16px 0px 0px 0px;
        }
    }
    // .popup__alert-error

    &__alert-error {
        color: #f23c3c;
        padding: 20px;
        margin-top: 1.5rem;
        font-size: 14px;
        width: 100%;
        border-radius: 8px;
        text-align: center;
        background: linear-gradient(#f23c3c1f, #f23c3c1f), #f6faff;
        line-height: 150%;
    }

    // .popup__bottom

    &__bottom {
    }

    // .popup__footer

    &__footer {
    }
    // .popup__bottom

    &__bottom {
    }
    // .popup__footer

    &__footer {
    }
    // .popup__subtitle

    &__subtitle {
        margin-top: 0.5rem;
        text-align: center;
        font-size: 14px;
    }
    // .popup__status-icon

    &__status-icon {
        margin-bottom: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            width: 180px;
            height: 180px;
        }
    }
}

[disabled] {
    pointer-events: none;
    opacity: 0.56;
}

.popup__inputs._form-error.filled {
    background-color: #f23c3c1f;
    box-shadow: none;
    label {
        position: relative;
        transform: translateY(-0.1rem);
        color: #e33333;
        font-size: 0.8156rem;
    }
}
.popup__inputs._form-focus.filled {
    label {
        color: #222d41;
    }
}
.popup__inputs.filled {
    label {
        position: relative;
        transform: translateY(-0.1rem);
        color: #222d4185;
        font-size: 0.8156rem;
    }
}

.bottom-popup .popup__inputs._form-error._form-focus {
    background: transparent;
    border: 1px solid #f23c3c;
    box-shadow: none;
    label {
        position: relative;
        transform: translateY(-0.1rem);
        color: #e33333;
        font-size: 0.8156rem;
    }
}

.form-error {
    height: 0;
    line-height: 1.5rem;
    color: #e33333;
    font-size: 0.8156rem;
    transform: translateY(-20%);
    opacity: 0;
    transition:
        transform 0.8s ease,
        opacity 0.3s ease, height 0.3s ease;
    &._active {
        height: 24px;
        transform: translateY(0);
        opacity: 1;
    }
}

.bottom-popup {
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 18px 0px;
    .popup__inputs {
        height: 2.75rem;
        padding: 3px 10px;
        display: flex;
        margin-bottom: 0px;
        border-radius: 0.5rem;
        &.hidden {
            display: none;
        }
        &._form-error {
            background-color: #f23c3c1f;
            box-shadow: none;
            label {
            }
        }
        label {
            font-size: 14px;
        }
        input {
            font-size: 12px;
            line-height: 1;
        }
    }
    // .bottom-popup__title

    &__title {
        display: flex;
        font-size: 14px;
        justify-content: space-between;

        &.save-title {
            justify-content: start;
            align-items: center;
        }
    }

    // .bottom-popup__icon

    &__icon {
        position: relative;
        display: flex;
        padding: 2px;
        margin: 0px 0px 0px 5px;
        cursor: pointer;
        svg {
            fill: #222d4152;
        }
        &:hover {
            .bottom-popup__tippy {
                opacity: 1;
                pointer-events: auto;
            }
        }
    }

    // .bottom-popup__tippy

    &__tippy {
        position: absolute;
        opacity: 0;
        pointer-events: none;
        background-color: #575b61;
        color: #fff;
        bottom: 150%;
        left: 50%;
        transform: translate(-50%, 0);
        z-index: 10;
        width: 288px;
        line-height: 1.25rem;
        padding: 0.75rem 1rem;
        border-radius: 0.75rem;
        transition: opacity 0.3s ease 0s;
        &::before {
            content: "";
            position: absolute;
            bottom: -8px;
            left: 50%;
            inline-size: 0.5rem;
            block-size: 0.5rem;
            border-radius: 0.125rem;
            box-sizing: border-box;
            background: inherit;
            transform: translate(-50%, -50%) rotate(45deg);
            @media (max-width: $mobileSmall) {
                left: 90%;
            }
        }
        @media (max-width: $mobileSmall) {
            left: 200%;
            transform: translate(-100%, 0);
            font-size: 14px;
            padding: 5px 10px;
        }
    }
}

.switch {
    position: relative;
    display: inline-block;
    width: 48px;
    height: 24px;
    overflow: hidden;
    cursor: pointer;

    &__checkbox {
        display: none;
        &:checked + .switch__slider {
            background-color: #2e71fc;
        }
        &:checked + .switch__slider::after {
            opacity: 0;
        }
        &:checked + .switch__slider::before {
            transform: translateX(24px);
        }
        &:checked + .switch__slider .switch__check {
            left: 0;
        }
    }

    // .switch__check
    &__check {
        pointer-events: none;
        display: block;
        width: 24px;
        height: 24px;
        position: absolute;
        top: 50%;
        left: -25px;
        transform: translate(0px, -50%);
        z-index: 10;
        transition: left 0.3s ease;
        svg {
            fill: #fff;
        }
    }

    &__slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #e2e8ef;
        border-radius: 2rem;
        transition: 0.3s;
        &:hover {
            background-color: #a2a8ae;
        }
        &::before {
            content: "";
            position: absolute;
            height: 16px;
            width: 16px;
            left: 5px;
            top: 4px;
            background-color: white;
            -webkit-transition: 0.4s;
            transition: 0.4s;
            border-radius: 50%;
        }
        &::after {
            content: "";
            transition-property: transform;
            transition-duration: 150ms;
            transition-timing-function: ease-in-out;
            position: absolute;
            top: 50%;
            transform: translate(0px, -50%);
            right: 5px;
            border-radius: 50%;
            width: 12px;
            height: 12px;
            border: 2px solid #a2a8ae;
        }
    }
}

.footer-popup {
    margin-top: 2rem;
    @media (max-width: $mobile) {
        margin-top: 1.5rem;
    }
    // .footer-popup__images

    &__images {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        gap: 16px;
        svg {
            filter: brightness(0) saturate(100%) invert(93%) sepia(6%) saturate(513%) hue-rotate(177deg) brightness(88%)
                contrast(88%);
        }
    }

    // .footer-popup__copy

    &__copy {
        margin-top: 1.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        svg {
            fill: rgb(192, 201, 212);
        }
    }
}

#success {
    .popup__button {
        pointer-events: auto;
        opacity: 1;
        margin-bottom: 0px;
    }
    .popup__title {
        font-size: 20px;
        text-align: center;
        font-weight: 500;
        margin: 0px 0px 0px 0px;
    }
    .popup__price {
        justify-content: center;
        margin-top: 1rem;
        text-align: center;
        font-size: 28px;
        @media (max-width: $mobile) {
            font-size: 20px;
        }
    }
}

.popup {
    // .popup__animation

    &__animation {
    }

    // .popup__skeleton

    &__skeleton {
        color: transparent;
        background: #f6faff !important;
        box-shadow: none !important;
        filter: contrast(0) brightness(0);
        animation: tuiSkeleton ease-in-out 1s infinite alternate;
        -webkit-user-select: none;
        user-select: none;
        pointer-events: none;
        height: 56px;
        border-radius: 8px;
        width: 100%;
        margin-bottom: 0.75rem;
        &.last {
            margin-bottom: 1.5rem;
        }
        &.small {
            height: 20px;
        }
    }
}

@keyframes tuiSkeleton {
    0% {
        opacity: 0.03;
    }
    to {
        opacity: 0.06;
    }
}

/* Базовые стили */
.popup__body {
    opacity: 0; /* Начальная прозрачность */
    visibility: hidden; /* Скрываем элемент */
    transition:
        opacity 1s ease,
        visibility 1s ease; /* Плавный переход */
}

.popup__body._active {
    opacity: 1; /* Делаем видимым */
    visibility: visible; /* Показываем элемент */
}

.popup__animation {
    position: absolute;
    top: 40px;
    left: 40px;
    width: calc(100% - 80px);
    height: calc(100% - 80px);
    opacity: 1; /* Начальная прозрачность */
    visibility: visible; /* Показываем элемент */
    transition:
        opacity 0.5s ease,
        visibility 0.5s ease; /* Плавный переход */
}

.popup__animation.hidden {
    opacity: 0; /* Делаем невидимым */
    visibility: hidden; /* Скрываем элемент */
}

.popup {
    // .popup__warning

    &__warning {
        position: absolute;
        top: 40px;
        left: 40px;
        width: calc(100% - 80px);
        display: flex;
        justify-content: space-between;
        background-color: #f1c90a1f;
        gap: 0.5rem;
        padding: 1rem;
        border-radius: 0.75rem;
        img {
            width: 24px;
            height: 24px;
        }
    }

    // .popup__warning-text

    &__warning-text {
        display: flex;
        flex-direction: column;
        line-height: 1.5rem;
        span {
            font-weight: 700;
        }
    }
}
.warning-close {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
        fill: #222d4152;
    }
}

.popup__warning,
.popup__body-2 {
    transition:
        opacity 0.5s ease,
        visibility 0.5s ease;
}

.popup__warning.hidden,
.popup__body-2.hidden {
    opacity: 0;
    visibility: hidden;
}

.popup__body-2 {
    opacity: 0;
    visibility: hidden;
    transition:
        opacity 0.5s ease,
        visibility 0.5s ease;
}

.popup__body-2._active {
    opacity: 1;
    visibility: visible;
}
